    // doc.output('dataurlnewwindow')
    this.data=doc.output('dataurl');
    const fileName = `${customerDetails.custAccount[0].accountId}` + new Date().getDate() + '_' + (new Date().getMonth() + 1) + '_' + new Date().getFullYear();
    this.shareObjects.file=fileName+'.pdf';
  
    // doc.save(fileName+'.pdf');
    // console.log(this.data);
    let emailId='gayathri.r@rumango.com';

    // this.shareObjects.file='210614_11_2022 (1).pdf';
